.container {
    min-width: 320px;
    margin: 0 auto;
    padding: 0 20px;

    @media screen and (min-width: 480px) {
        min-width: 480px;
    }

    @media screen and (min-width: 768px) {
        min-width: 768px;
        padding: 0 76px;
    }

    @media screen and (min-width: 1024px) {
        min-width: 1024px;
        padding: 0 0;
    }
}